}

@media (prefers-color-scheme:light) {
    #page {
        /* background-color: rgb(225, 229, 233); */
        /* color: rgb(44, 44, 44); */
    }
    .bento {    
        > * {
            background: var(--accent-gradient)
        }
    }
}